@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{font-family:'Roboto', sans-serif}body{background-color:#737372;overflow-x:hidden}.container{max-width:1280px;margin:0 auto}@media only screen and (max-width: 767px){body{margin:10px}}

.hero{background-image:linear-gradient(to top, rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/air-quality/static/media/hero-bg.09a251a4.jpg);background-size:cover;padding:80px 0 178px}.hero-partners{display:flex;align-items:center}.hero-partners-item{margin-left:60px}.hero-partners-item:first-child{margin-left:0}.hero-navigation{margin-top:45px}.hero-navigation-item{font-weight:normal;font-size:19px;line-height:22px;color:#ffffff;margin-left:74px;text-decoration:none}.hero-navigation-item:first-child{margin-left:0}.hero-title{font-weight:normal;font-size:57px;line-height:67px;color:#ffffff;margin:108px 0 132px;width:664px}.hero-link{display:inline-block;padding:35px 151px;text-decoration:none;font-weight:normal;font-size:32px;line-height:37px;color:#ffffff;background:#b78a3f}

.facts{position:relative;padding:100px 60px 50px;box-sizing:border-box}.facts-title{font-weight:normal;font-size:35px;line-height:41px;color:#ffffff;text-align:center;margin-bottom:30px}.facts-slide-desc{margin-top:40px;font-weight:normal;font-size:27px;line-height:32px;text-align:center;color:#ffffff}.facts>.swiper-button-prev,.facts .swiper-button-next{color:#b78a3f}.facts>.swiper-button-prev{left:10px}.facts>.swiper-button-next{right:10px}.facts>.swiper-button-disabled{opacity:0}.facts-pagination{display:flex;justify-content:center;margin-top:66px}.facts-pagination-bullet{display:inline-block;width:24px;height:24px;border-radius:50%;margin:0 20px;background-color:transparent;border:2px solid #b78a3f;opacity:1;transition:0.3s background-color}.facts-pagination>.swiper-pagination-bullet-active{background-color:#b78a3f}

.sectors{padding:120px 0 50px}.sectors-title{font-weight:normal;font-size:35px;line-height:41px;color:#ffffff;text-align:center}.sectors-subtitle{font-weight:normal;font-size:23px;line-height:27px;color:#ffffff;margin:42px 0 30px}.sectors-list{display:flex;flex-wrap:wrap;justify-content:space-between}.sectors-list-item{position:relative;width:calc((100% / 3) - 70px);height:240px;display:flex;align-items:center;margin-bottom:30px}.sectors-list-item-name{width:100%;text-align:center;font-size:23px;line-height:30px;color:#222222;background:rgba(255,255,255,0.83)}.sectors-list-item-name-switched{height:100%;-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip{position:absolute;left:100%;width:400px;pointer-events:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;z-index:1;background:#dadada;padding:16px 16px 16px 75px;-webkit-clip-path:polygon(100% 0, 100% 100%, 12% 100%, 0 50%, 12% 0);clip-path:polygon(100% 0, 100% 100%, 12% 100%, 0 50%, 12% 0);-webkit-animation:fadeIn 0.4s ease-in-out;animation:fadeIn 0.4s ease-in-out}.sectors-list-item-tooltip-switched{left:auto;left:initial;right:100%;padding:16px 75px 16px 16px;-webkit-clip-path:polygon(85% 0, 100% 50%, 85% 100%, 0 100%, 0 0);clip-path:polygon(85% 0, 100% 50%, 85% 100%, 0 100%, 0 0)}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.footer{margin-top:150px;border-top:1px solid #ffffff}.footer-partners{display:flex;align-items:center}.footer-partners-item{width:7%;margin-left:130px}.footer-partners-item:first-child{margin-left:0;width:10%}.footer-partners-item:last-child{width:10%}.footer-inner{display:flex;justify-content:space-between}.footer-navigation{display:flex;flex-direction:column}.footer-navigation-item{font-weight:normal;font-size:17px;line-height:20px;color:#ffffff;text-decoration:none;margin:10px 0}.footer-author{width:366px;font-weight:normal;font-size:17px;line-height:20px;color:#ffffff;margin:20px 0}.footer-author span{color:#eee851}.footer-detailis{margin:70px 0 11px}.footer-detailis-text{margin-top:14px;font-style:normal;font-weight:300;font-size:14px;line-height:14px;color:#ffffff}.footer-detailis-privacy{margin-top:14px}.footer-detailis-privacy input{margin-left:0}.footer-detailis-privacy label{font-style:normal;font-weight:300;font-size:14px;line-height:14px;color:#ffffff}.footer-detailis-link{font-weight:normal;font-weight:300;font-size:14px;line-height:14px;color:#ffffff;text-decoration:none;cursor:pointer}

.air-q{padding-top:99px}.air-q-title{font-weight:normal;font-size:35px;line-height:41px;text-align:center;color:#ffffff}.air-q-map{font-weight:normal;margin:20px 0;height:560px}.air-q-text{font-size:23px;line-height:27px;color:#ffffff}

